import { randomNumber, backgroundQuery } from './utilities';
import backImg from '../assets/img/bg3.jpg';


export default class Api {
  constructor(localization, lang = 'en') {
    this.translate = {
      url: 'https://translate.yandex.net/api/v1.5/tr.json/translate',
      key: 'trnsl.1.1.20191210T120439Z.06eb02be3c491030.20c7fb31ce96d9ad2861c9bb277e1b6eccd3486d',
      query: 'Minsk, Belarus',
      langTo: lang, // be, en
      searchLang: 'en',
    };

    this.weather = {
      url: 'http://api.openweathermap.org/data/2.5/weather',
      key: '7d6866f1662d125add334b76fc224e1f',
      city: 'Minsk',
      coordinates: {
        lat: '27.56',
        lon: '53.9',
      },
      response: {},
    };

    this.weatherWeek = {
      url: 'http://api.openweathermap.org/data/2.5/forecast',
      key: '7d6866f1662d125add334b76fc224e1f',
      cityID: 625144,
      days: 3,
    };

    this.background = {
      url: 'https://api.unsplash.com/search/photos',
      key: '4b65e965dafd01dd64d63fff90e2d82ca1d42c74df27c8e896b67038dc028e10',
      query: 'weather winter morning',
      elem: document.body,
    };

    this.geoposition = {
      url: 'http://ip-api.com/json',
    };

    this.respond = {
      weather: {
        country: '',
        countryCode: '',
        city: '',
        ts: '',
        cityCountry: '',
        temperatureVal: '',
        stateVal: '',
        feelslikeVal: '',
        humidityVal: '',
        windVal: '',
        iconVal: '',
      },
      weatherWeek: [],
      map: {
        latVal: '',
        lonVal: '',
      },
    };

    this.localization = localization;
  }

  async geoPosition() {
    const { url } = this.geoposition;
    let translQuery;
    try {
      const sendRequest = await fetch(url);
      if (!sendRequest.ok) throw new Error(sendRequest.statusText);
      const dataRespond = await sendRequest.json();
      this.geoposition.response = dataRespond;
      this.weather.city = dataRespond.city;
      this.translate.query = `${dataRespond.city}, ${dataRespond.country}`;
      translQuery = this.translateLocation();
    } catch (err) {
      global.alert(err);
    }
    return translQuery;
  }

  async translateLocation(lone = null) {
    const {
      url, key, query, langTo,
    } = this.translate;
    let wthrFull;
    try {
      const sendRequest = await fetch(`${url}?key=${key}&text=${query}&lang=${langTo}`);
      if (!sendRequest.ok) throw new Error(sendRequest.statusText);
      const dataRespond = await sendRequest.json();
      this.translate.response = dataRespond;
      this.respond.weather.cityCountry = dataRespond.text;
      if (lone) {
        wthrFull = this.respond;
      } else {
        wthrFull = this.getLocationWeatherFull();
      }
    } catch (err) {
      global.alert(err);
    }
    return wthrFull;
  }

  async getLocationWeatherFull() {
    const {
      url, key, city, coordinates: { lat, lon },
    } = this.weather;
    let weatherWeek;
    try {
      const addUrl = (city !== null) ? `?q=${city}` : `?lat=${lat}&lon=${lon}`;
      const sendRequest = await fetch(`${url}${addUrl}&appid=${key}`);
      if (!sendRequest.ok) throw new Error(sendRequest.statusText);
      const dataRespond = await sendRequest.json();
      this.weather.response = dataRespond;
      const {
        weather, main, id, wind, coord, sys, name, timezone,
      } = dataRespond;
      this.weatherWeek.cityID = id;
      this.respond.weather.country = this.localization.getCountryByCode(sys.country);
      this.respond.weather.countryCode = sys.country;
      this.respond.weather.city = name;
      this.translate.query = `${name}, ${this.respond.weather.country}`;
      this.respond.weather.ts = Math.round((Number(new Date() / 1000))) + parseInt(timezone, 10);
      this.respond.weather.temperatureVal = Math.round(main.temp - 273);
      this.respond.weather.stateVal = weather[0].description;
      this.respond.weather.feelslikeVal = Math.round(main.feels_like - 273);
      this.respond.weather.humidityVal = main.humidity;
      this.respond.weather.windVal = wind.speed;
      this.respond.weather.iconVal = `http://openweathermap.org/img/wn/${weather[0].icon}@2x.png`;
      this.respond.map.latVal = coord.lat;
      this.respond.map.lonVal = coord.lon;
      this.background.query = backgroundQuery(weather[0].description);
      this.getBackground();
      weatherWeek = this.getLocationWeatherForecast();
    } catch (err) {
      global.alert(err);
    }
    return weatherWeek;
  }

  async getLocationWeatherForecast() {
    const {
      url, key, cityID, days,
    } = this.weatherWeek;
    try {
      const sendRequest = await fetch(`${url}?id=${cityID}&cnt=${days * 8 + 1}&appid=${key}`);
      if (!sendRequest.ok) throw new Error(sendRequest.statusText);
      const dataRespond = await sendRequest.json();
      this.weather.response = dataRespond;
      const { list } = dataRespond;
      this.respond.weatherWeek = [list[8], list[16], list[24]].map(item => ({
        day: item.dt,
        temp: Math.round(item.main.temp - 273),
        icon: `http://openweathermap.org/img/wn/${item.weather[0].icon}@2x.png`,
      }));
    } catch (err) {
      global.alert(err);
    }
    return this.respond;
  }

  async getBackground() {
    const { url, key, query } = this.background;
    try {
      const sendRequest = await fetch(`${url}?query=${query}&client_id=${key}`);
      if (!sendRequest.ok) throw new Error(sendRequest.statusText);
      const dataRespond = await sendRequest.json();
      this.background.response = dataRespond;
      this.setBackground();
    } catch (err) {
      global.alert(err);
    }
  }

  async translateQuery(query, lang) {
    const { url, key } = this.translate;
    let translated;
    try {
      const sendRequest = await fetch(`${url}?key=${key}&text=${query}&lang=${lang}`);
      if (!sendRequest.ok) throw new Error(sendRequest.statusText);
      const dataRespond = await sendRequest.json();
      translated = dataRespond.text;
    } catch (err) {
      global.alert(err);
    }
    return translated;
  }

  setBackground() {
    const { response, elem } = this.background;
    const img = new Image();
    img.src = response.results[randomNumber(0, (response.results.length - 1))].urls.full;
    img.addEventListener('load', () => {
      elem.style.backgroundImage = `url(${img.src})`;
    });
    img.addEventListener('error', () => {
      elem.style.backgroundImage = `url(${backImg.src})`;
    });
  }
}
