import { createDomEl } from '../utilities';
import * as micSvg from '../../assets/img/mic.svg';

export default class Search {
  constructor(localData) {
    this.local = localData;
    this.searchCont = createDomEl('div', 'search', null, null, null);
  }

  init() {
    const { placeholder, button } = this.local.ui;
    const form = createDomEl('form', null, this.searchCont, null, null);

    createDomEl('input', 'query', form, null, {
      name: 'querySearch',
      type: 'text',
      placeholder,
    });

    const micButton = createDomEl('button', 'search_voice', form, null, null);
    createDomEl('img', null, micButton, null, {
      src: micSvg,
      alt: 'voice search',
    });

    createDomEl('input', 'submit', form, null, {
      value: button,
      type: 'submit',
    });
    return this.searchCont;
  }

  localize() {
    const { placeholder, button } = this.local.ui;

    this.searchCont.querySelector('.query').placeholder = placeholder;
    this.searchCont.querySelector('.submit').value = button;
  }
}
