import { createDomEl } from '../utilities';

export default class Panel {
  constructor(localData) {
    this.local = localData;
    this.lang = '';
    this.tempFormat = '';
    this.optionData = ['en', 'ru', 'be'];
    this.optionsCont = createDomEl('div', 'options', null, null, null);
  }

  init() {
    const ulOptions = createDomEl('ul', null, this.optionsCont, null, null);

    createDomEl('li', 'options__bgupdate', ulOptions, null, null);

    const liLang = createDomEl('li', 'options__lang', ulOptions, null, null);
    const selectLang = createDomEl('select', 'lang', liLang, null, null);

    this.optionData.forEach((option) => {
      if (option === this.lang) {
        createDomEl('option', `lang__${option}`, selectLang, option, { value: option, selected: 'selected' });
      } else {
        createDomEl('option', `lang__${option}`, selectLang, option, { value: option });
      }
    });

    const liTemp = createDomEl('li', 'options__tempswitch', ulOptions, null, null);
    if (this.tempFormat === 'c') {
      createDomEl('button', null, liTemp, 'F', { type: 'Fahr' });
      createDomEl('button', 'active', liTemp, 'C', { type: 'Cel' });
    } else {
      createDomEl('button', 'active', liTemp, 'F', { type: 'Fahr' });
      createDomEl('button', null, liTemp, 'C', { type: 'Cel' });
    }

    return this.optionsCont;
  }
}
