import { createDomEl, toFahrenheit } from '../utilities';

export default class Weatherweek {
  constructor(localData) {
    this.localization = '';
    this.local = localData;
    this.stat = [];
    this.tempFormat = '';
    this.weatherWeekCont = createDomEl('ul', 'weather__week', null, null, null);
  }

  init() {
    this.stat.forEach((item) => {
      const liDay = createDomEl('li', null, this.weatherWeekCont, null, null);
      createDomEl('p', null, liDay, item.day, null);
      const divWeather = createDomEl('div', null, liDay, null, null);
      const temperatureConverted = (this.tempFormat === 'c') ? item.temp : toFahrenheit(item.temp);
      createDomEl('span', null, divWeather, temperatureConverted, null);
      createDomEl('img', null, divWeather, null, {
        src: item.icon,
        alt: 'weather-info',
      });
    });

    return this.weatherWeekCont;
  }

  update() {
    Object.values(this.weatherWeekCont.children).forEach((child, index) => {
      const el = child.children;
      const temperatureConverted = (this.tempFormat === 'c') ? this.stat[index].temp : toFahrenheit(this.stat[index].temp);
      el[1].children[0].innerText = temperatureConverted;
      el[1].children[1].src = this.stat[index].icon;
    });
  }

  localize() {
    Object.values(this.weatherWeekCont.children).forEach((child, index) => {
      const el = child.children;
      el[0].innerText = this.stat[index].day;
    });
  }
}
