module.exports = {
  toFahrenheit: grad => (grad * 9 / 5 + 32),

  toCelsium: grad => ((grad - 32) * 5 / 9),

  createDomEl: (tagName, className, tagTo, tagValue, tagAttr = null) => {
    const domEl = document.createElement(tagName);

    if (className !== null) {
      domEl.setAttribute('class', className);
    }
    if (tagValue !== null) {
      domEl.innerText = tagValue;
    }
    if (tagAttr !== null) {
      Object.keys(tagAttr).forEach((elem) => {
        domEl.setAttribute(elem, tagAttr[elem]);
      });
    }
    if (tagTo !== null) {
      tagTo.append(domEl);
    }
    return domEl;
  },

  Localstorage: class Localstorage {
    constructor(key) {
      this.key = key;
    }

    saveData(someData) {
      if (someData !== '') {
        localStorage.setItem(this.key, JSON.stringify(someData));
      }
    }

    restoreData() {
      let dataOut = '';
      if (localStorage.getItem(this.key)) {
        dataOut = JSON.parse(localStorage.getItem(this.key));
      } else {
        dataOut = false;
      }
      return dataOut;
    }
  },

  Geoposition: class Geoposition {
    constructor() {
      this.coords = {};
      this.mess = false;
    }

    static getCurrPos(options = {}) {
      return new Promise((resolve, reject) => {
        navigator.geolocation.getCurrentPosition(resolve, reject, options);
      });
    }

    async getAddress() {
      this.mess = true;
      const position = await Geoposition.getCurrPos();
      const { latitude, longitude } = position.coords;
      return {
        lat: latitude,
        lon: longitude,
      };
    }
  },

  randomNumber: (min, max) => Math.floor(Math.random() * (1 + min - max)) + max,

  backgroundQuery: (query) => {
    const date = new Date();
    let partDay = '';
    if (date.getHours() >= 22 || date.get >= 4) {
      partDay = 'night';
    } else if (date.getHours() >= 5 && date.getHours() <= 12) {
      partDay = 'morning';
    } else if (date.getHours() >= 13 && date.getHours() <= 17) {
      partDay = 'afternoon';
    } else if (date.getHours() >= 18 && date.getHours() <= 21) {
      partDay = 'evening';
    }

    let partYear = '';
    if (date.getMonth() === 11 || date.getMonth() === 0 || date.getMonth() === 1) {
      partYear = 'winter';
    } else if (date.getMonth() === 2 || date.getMonth() === 3 || date.getMonth() === 4) {
      partYear = 'spring';
    } else if (date.getMonth() === 5 || date.getMonth() === 6 || date.getMonth() === 7) {
      partYear = 'summer';
    } else if (date.getMonth() === 8 || date.getMonth() === 9 || date.getMonth() === 10) {
      partYear = 'autumn';
    }
    return `forest ${partYear} ${partDay} ${query}`;
  },
};
