import { createDomEl } from '../utilities';

export default class Map {
  constructor(localData) {
    this.stat = '';
    this.local = localData;
    this.mapCont = createDomEl('div', 'map__container', null, null, null);
    this.maps = {
      url: 'https://www.google.com/maps/embed/v1/place',
      key: 'key=AIzaSyCwqpCJkh67hvkqWzk7iSTUGcFTxme4dPk',
    };
  }

  init() {
    const { lat, lon } = this.local.ui;
    const { url, key } = this.maps;
    const { latVal, lonVal } = this.stat;
    const map = createDomEl('div', 'map', this.mapCont, null, null);

    createDomEl('iframe', null, map, null, {
      width: 375,
      height: 375,
      src: `${url}?${key}&q=${latVal},${lonVal}`,
    });

    const ulInfo = createDomEl('ul', null, this.mapCont, null, null);
    const liInfoLong = createDomEl('li', 'longitude', ulInfo, null, null);
    const [lonDeg, lonMin] = lonVal.toString().split('.');
    createDomEl('span', 'lon', liInfoLong, lon, null);
    createDomEl('span', 'deg', liInfoLong, lonDeg, null);
    createDomEl('span', 'min', liInfoLong, lonMin, null);

    const liInfoLat = createDomEl('li', 'lattitude', ulInfo, null, null);
    const [latDeg, latMin] = latVal.toString().split('.');
    createDomEl('span', 'lat', liInfoLat, lat, null);
    createDomEl('span', 'deg', liInfoLat, latDeg, null);
    createDomEl('span', 'min', liInfoLat, latMin, null);
    return this.mapCont;
  }

  update() {
    const { url, key } = this.maps;
    const { latVal, lonVal } = this.stat;

    const iframe = this.mapCont.querySelector('iframe');
    iframe.src = `${url}?${key}&q=${latVal},${lonVal}`;

    const [lonDeg, lonMin] = lonVal.toString().split('.');
    this.mapCont.querySelector('.longitude .deg').innerText = lonDeg;
    this.mapCont.querySelector('.longitude .min').innerText = lonMin;

    const [latDeg, latMin] = latVal.toString().split('.');
    this.mapCont.querySelector('.lattitude .deg').innerText = latDeg;
    this.mapCont.querySelector('.lattitude .min').innerText = latMin;
  }

  localize() {
    const { lat, lon } = this.local.ui;

    this.mapCont.querySelector('.longitude .lon').innerText = lon;
    this.mapCont.querySelector('.lattitude .lat').innerText = lat;
  }
}
