import { createDomEl, toFahrenheit } from '../utilities';

export default class Weather {
  constructor(localData) {
    this.local = localData;
    this.stat = '';
    this.tempFormat = '';
    this.weatherCont = createDomEl('div', 'weather__info', null, null, null);
  }

  init() {
    const { wind, humidity, feels } = this.local.ui;
    const {
      temperatureVal, iconVal, feelslikeVal, windVal, humidityVal,
    } = this.stat;
    const temperatureConverted = (this.tempFormat === 'c') ? temperatureVal : toFahrenheit(temperatureVal);
    createDomEl('p', 'weather__info__temperature', this.weatherCont, temperatureConverted, null);

    createDomEl('img', null, this.weatherCont, null, {
      src: iconVal,
      alt: 'weather-info',
    });

    const ulStats = createDomEl('ul', 'weather__info__stats', this.weatherCont, null, null);

    const temperatureFeelsConverted = (this.tempFormat === 'c') ? feelslikeVal : toFahrenheit(feelslikeVal);
    const liFeelsLike = createDomEl('li', 'feels__like', ulStats, null, null);
    createDomEl('span', 'feels__like__label', liFeelsLike, feels, null); // local
    createDomEl('span', 'feels__like__value', liFeelsLike, temperatureFeelsConverted, null); // update

    const liWind = createDomEl('li', 'wind', ulStats, null, null);
    createDomEl('span', 'wind__label', liWind, wind, null); // local
    createDomEl('span', 'wind__value', liWind, windVal, null); // update

    const liHumidity = createDomEl('li', 'humidity', ulStats, null, null);
    createDomEl('span', 'humidity__label', liHumidity, humidity, null); // local
    createDomEl('span', 'humidity__value', liHumidity, humidityVal, null); // update
    return this.weatherCont;
  }

  update() {
    const {
      temperatureVal, iconVal, feelslikeVal, windVal, humidityVal,
    } = this.stat;
    const temperatureConverted = (this.tempFormat === 'c') ? temperatureVal : toFahrenheit(temperatureVal);
    this.weatherCont.querySelector('.weather__info__temperature').innerText = temperatureConverted;
    this.weatherCont.querySelector('img').src = iconVal;
    const temperatureFeelsConverted = (this.tempFormat === 'c') ? feelslikeVal : toFahrenheit(feelslikeVal);
    this.weatherCont.querySelector('.feels__like__value').innerText = temperatureFeelsConverted;
    this.weatherCont.querySelector('.wind__value').innerText = windVal;
    this.weatherCont.querySelector('.humidity__value').innerText = humidityVal;
  }

  localize() {
    const { wind, humidity, feels } = this.local.ui;

    this.weatherCont.querySelector('.feels__like__label').innerText = feels;
    this.weatherCont.querySelector('.wind__label').innerText = wind;
    this.weatherCont.querySelector('.humidity__label').innerText = humidity;
  }
}
