import { createDomEl } from '../utilities';
import Panel from './panel';
import Search from './search';
import Weather from './weather_widget';
import Weatherweek from './weather_week';
import Map from './map';

export default class Body {
  constructor(local, api, tempFormat) {
    this.local = local;
    this.tempFormat = tempFormat;
    this.stat = '';
    this.api = api;
    this.body = document.body;
    this.wrapper = createDomEl('div', 'wrapper', this.body, null, null);
    this.panel = new Panel(this.local);
    this.search = new Search(this.local.setLocalSearchData());
    this.weather = new Weather(this.local.setLocalWeatherData());
    this.weatherWeek = new Weatherweek(this.local.setLocalWeatherWeekData());
    this.maps = new Map(this.local.setLocalMapData());
  }

  init() {
    const { ts, city, country } = this.stat.weather;
    const header = createDomEl('header', null, this.wrapper, null, null);
    this.panel.tempFormat = this.tempFormat;
    this.panel.lang = this.local.lang;
    header.append(this.panel.init());
    header.append(this.search.init());
    const main = createDomEl('main', null, this.wrapper, null, null);
    const sectWeather = createDomEl('section', 'weather', main, null, null);
    createDomEl('h2', '', sectWeather, '', null);
    this.api.translateQuery(`${city}, ${country}`, this.local.lang).then((data) => {
      const [locationData] = data;
      this.body.querySelector('h2').innerText = locationData;
    });
    createDomEl('h4', '', sectWeather, this.local.getFullFormatDate(ts), null);
    this.weather.tempFormat = this.tempFormat;
    this.weather.stat = this.stat.weather;
    sectWeather.append(this.weather.init());
    this.weatherWeek.tempFormat = this.tempFormat;
    this.weatherWeek.stat = this.stat.weatherWeek.map(item => ({
      day: this.local.getDayName(item.day),
      temp: item.temp,
      icon: item.icon,
    }));
    sectWeather.append(this.weatherWeek.init());
    const sectMap = createDomEl('section', 'location', main, null, null);
    this.maps.stat = this.stat.map;
    sectMap.append(this.maps.init());
    return this.body;
  }

  update() {
    const { ts, city, country } = this.stat.weather;
    this.api.translateQuery(`${city}, ${country}`, this.local.lang).then((data) => {
      const [locationData] = data;
      this.body.querySelector('h2').innerText = locationData;
    });
    this.body.querySelector('h4').innerText = this.local.getFullFormatDate(ts);

    this.weather.stat = this.stat.weather;
    this.weather.update();

    this.weatherWeek.stat = this.stat.weatherWeek.map(item => ({
      day: this.local.getDayName(item.day),
      temp: item.temp,
      icon: item.icon,
    }));
    this.weatherWeek.update();

    this.maps.stat = this.stat.map;
    this.maps.update();
  }

  localize() {
    const { ts, city, country } = this.stat.weather;
    this.api.translate.query = `${city}, ${country}`;
    this.api.translateQuery(`${city}, ${country}`, this.local.lang).then((data) => {
      const [locationData] = data;
      this.body.querySelector('h2').innerText = locationData;
    });
    this.body.querySelector('h4').innerText = this.local.getFullFormatDate(ts);

    this.search.local = this.local.setLocalSearchData();
    this.search.localize();

    this.weather.local = this.local.setLocalWeatherData();
    this.weather.localize();

    this.weatherWeek.local = this.local.setLocalWeatherWeekData();

    this.weatherWeek.stat = this.stat.weatherWeek.map(item => ({
      day: this.local.getDayName(item.day),
      temp: item.temp,
      icon: item.icon,
    }));
    this.weatherWeek.localize();

    this.maps.local = this.local.setLocalMapData();
    this.maps.localize();
  }

  tempFormatter() {
    this.weather.tempFormat = this.tempFormat;
    this.weather.update();
    this.weatherWeek.tempFormat = this.tempFormat;
    this.weatherWeek.update();
  }

  updateStats() {
    const { ts } = this.stat.weather;

    this.body.querySelector('h4').innerText = this.local.getFullFormatDate(ts);

    this.weather.stat = this.stat.weather;
    this.weather.update();

    this.weatherWeek.stat = this.stat.weatherWeek.map(item => ({
      day: this.local.getDayName(item.day),
      temp: item.temp,
      icon: item.icon,
    }));
    this.weatherWeek.update();
  }
}
